import React from "react"
import { StaticImage } from "gatsby-plugin-image"

import * as styles from "./linkcard.module.scss"

const LinkCard = ({ url, title, thumbnailUrl }) => {
  const uri = new URL(url)
  return (
    <div id={styles.link_card}>
      <div id={styles.card_body}>
        <p href={url} id={styles.link_card_title}>
          {title}
        </p>
        <p>{uri.host}</p>
      </div>
      <StaticImage
        src="https://avatars.githubusercontent.com/u/77187490?v=4?s=400"
        // id={styles.link_card_img}
        style={{ display: "inline-block", width: "10rem", height: "10rem" }}
        imgStyle={{ objectFit: "contain" }}
        alt="Github Image"
      />
      <a id={styles.link_card_root} href={url}>
        {" "}
      </a>
    </div>
  )
}

export default LinkCard
